import React, {Fragment, useState} from 'react';
import CreditCardInput from 'react-credit-card-input';
import {
    OrderTitle, OrderSubtitle, InfoContainer, ContactInfoContainer, 
    OrderInfoContainer, ErrorText
} from '../components-jsx/ConfirmOrder.jsx';
import { 
    SignUpContainer
} from '../jsx/Confirm.jsx';
import {Button} from 'react-bootstrap';

// Item Template Data
// {
//     "id" : 1,
//     "name" : "Blue Dream 1/8 oz",
//     "description" : "Hybrid",
//     "longDescription": "This high quality leaf is meant to give you a calm, relaxing high. This will give you a focused yet calm high throughout the day.",
//     "measurement" : "24% THC",
//     "price" : 19.99,
//     "type" : "flower",
//     "imageURL" : "https://media.phillyvoice.com/media/images/01_030817_Dispensary_Carroll.2e16d0ba.fill-735x490.jpg"
//   },

const ConfirmOrder = (cartCount, cartTotal, confirmOrder, submitOrder, errorMessage, userData, orderConfirmed, confirmComplete, cardInfo, updateCardInfo) => {
    let name = "";
    let tel = "";
    
    if(orderConfirmed){
        return(
            <Fragment>
                 <OrderTitle>Order Confirmed!</OrderTitle>
                 <br/>
                 <OrderSubtitle>
                    Thank you for shopping with us!
                    <br/>
                    <br/>
                    <Button variant="success" onClick={() => {confirmComplete()}}>Back to Store</Button>
                 </OrderSubtitle>
                 
            </Fragment>
        )
    }
    const sendUpdatedCardInfo = (type, event) => {
        let newInfo = {...cardInfo};
        newInfo[type] = event.target.value;
        updateCardInfo(newInfo);
    }
    return(
        <Fragment>
            <OrderTitle> Confirm Order </OrderTitle>
            <ContactInfoContainer>
                <span>Contact Info</span>
                <br/>
                <br/>
                <SignUpContainer>
                    <input
                        type={"text"}
                        placeholder={"First and Last Name"}
                        maxLength={45}
                        onChange={(e)=>{name = e.target.value}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <input
                        type={"tel"}
                        placeholder={"Phone"}
                        maxLength={11}
                        onChange={(e)=>{tel = e.target.value}}
                    />
                </SignUpContainer>
                <SignUpContainer>
                    <CreditCardInput 
                        cardNumberInputProps={{ value: cardInfo.cardNumber, onChange: (event)=>{sendUpdatedCardInfo("cardNumber",event)} }}
                        cardExpiryInputProps={{ value: cardInfo.expiry, onChange: (event)=>{sendUpdatedCardInfo("expiry",event)} }}
                        cardCVCInputProps={{ value: cardInfo.cvc, onChange: (event)=>{sendUpdatedCardInfo("cvc",event)} }}
                        fieldClassName="input"
                        inputStyle={
                            {
                                background: "#404040",
                                color: "white",
                            }
                        }
                        fieldStyle={
                            {
                                background: "#404040",
                                color: "white",
                            }
                        }
                    />
                </SignUpContainer>
                
                <br/>
                <br/>
            </ContactInfoContainer>
            <OrderInfoContainer>
                Total Number of items: {cartCount}
                <br/>
                Tax:
                <br/>
                Delivery Fee:
                <br/>
                {`Total Cost: $${cartTotal.dollars}.` + (cartTotal.cents<10 ? `0` : ``) + `${cartTotal.cents}`}
                <br/>
                <br/>
                <Button variant="success" onClick={async() => {await submitOrder({name, tel})}}>Submit Order</Button>
                <br />
                <br />
                <ErrorText>{errorMessage}</ErrorText>
                
            </OrderInfoContainer>
        
        </Fragment>
    );
}

export default ConfirmOrder;
